import { defineStore } from 'pinia'

export default defineStore('currentUserStore', {
  state: () => {
    return {
      currentUser: {}
    }
  },
  actions: {
    clearUser () {
      this.currentUser = {}
    },
    getStorageUser () {
      if (localStorage.user) {
        this.currentUser = JSON.parse(localStorage.getItem('user')).user
      }
    }
  }
})
