  },
  methods: {
    ...mapActions(currentUserStore, ['clearUser', 'getStorageUser']),
    change (key) {
      this[key] = !this[key]
    },
    logout () {
      localStorage.removeItem('user')
      // currentUser.currentUser = {}
      this.clearUser()
      window.alert('登出成功')
    }
  },
  computed: {
    ...mapState(currentUserStore, ['currentUser'])
  },
  created () {
    this.getStorageUser()
  }
